<template>
  <div>
    <tb-checkbox v-model="disabledSingle" disabled>Checkbox</tb-checkbox>
    <tb-checkbox-group v-model="disabledGroup">
      <tb-checkbox label="" disabled></tb-checkbox>
      <tb-checkbox label="" disabled></tb-checkbox>
      <tb-checkbox label=""></tb-checkbox>
    </tb-checkbox-group>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        disabledSingle: true,
        disabledGroup: ['']
      }
    }
  }
</script>


